<template>
  <div class="home-page">
    <v-toolbar dense color="primary" :dark="true">
      <v-icon>mdi-people</v-icon>
      <v-toolbar-title>Random Picker</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-app-bar-nav-icon @click="showList"></v-app-bar-nav-icon>
    </v-toolbar>

    <center>
      <p v-if="!activeList" class="text small grey--text text--darken-2 ma-4">
        Create new list to start.
      </p>
    </center>

    <v-main class="winwheel-slot">
      <center v-if="activeList">
        <p class="text grey--text ma-4">
          {{
            activeList.options.length
              ? activeList.text
              : "Add items to the list to select."
          }}
        </p>
      </center>

      <div class="canvas-slot">
        <div class="indicator"></div>
        <div class="top-layer" @click="startSpin"></div>
        <canvas
          id="canvas"
          width="300"
          height="300"
          :style="{ transform: 'rotate(' + rotationAngle + 'deg)' }"
        >
          <center>
            <p>
              Sorry, your browser doesn't support canvas. Please try another.
            </p>
          </center>
        </canvas>
      </div>

      <center>
        <p class="text ma-4">{{ result }}</p>
      </center>
    </v-main>
  </div>
</template>

<script>
import { mapState } from "vuex";

// import TweenMax from "@/assets/js/TweenMax.min.js";
import Winwheel from "@/assets/js/WinWheel.min.js";

export default {
  name: "Home",

  data: () => ({
    theWheel: null,
    rotationAngle: 0,
    // Vars used by the code in this page to do power controls.
    wheelSpinning: false,
    result: null,
  }),

  methods: {
    showList() {
      this.$router.push({ name: "List" });
    },

    createWinwheel() {
      if (this.activeList) {
        const options = this.activeList.options;
        const numSegments = options.length;
        const outerRadius = 150;

        const segments = options.map((op) => {
          return {
            fillStyle: op.color,
            strokeStyle: "lightgrey",
            text: op.text,
          };
        });
        // Create new wheel object specifying the parameters at creation time.
        this.theWheel = new Winwheel({
          numSegments, // Specify number of segments.
          outerRadius, // Set outer radius so wheel fits inside the background.
          innerRadius: 20,
          textFontSize: 16, // Set font size as desired.
          // Define segments including colour and text.
          segments,
          // Specify the animation to use.
          animation: {
            type: "spinToStop",
            duration: 5, // Duration in seconds.
            spins: 8, // Number of complete spins.
            callbackFinished: this.alertPrize,
          },

          // Turn pins on.
          // pins: {
          //   number: numSegments,
          //   fillStyle: "lightgrey",
          //   outerRadius: 3
          // }
        });
      }
    },

    // -------------------------------------------------------
    // Click handler for spin button.
    // -------------------------------------------------------
    startSpin() {
      // Ensure that spinning can't be clicked again while already running.
      if (this.wheelSpinning == false) {
        // Set to true so that power can't be changed and spin button re-enabled during
        // the current animation. The user will have to reset before spinning again.
        this.wheelSpinning = true;

        this.randomPicker();
      } else {
        this.resetWheel();
      }
    },

    randomPicker() {
      const range = this.activeList.options.length;
      if (range) {
        // Begin the spin animation by calling startAnimation on the wheel object.
        this.startAnimation(range);
      }
    },

    startAnimation(range) {
      const goal = Math.floor(Math.random() * range);
      const arcsAngle = Math.round(360 / range);

      this.rotationAngle = (goal + 0.5) * arcsAngle * -1;
      this.rotationAngle += 90; // depend on indicator (+0 for top indicator)
      this.rotationAngle += 2160; // + 6 round

      setTimeout(() => {
        this.result = this.activeList.options[goal].text;
      }, 5000);
    },

    // -------------------------------------------------------
    //  for reset button.
    // -------------------------------------------------------
    resetWheel() {
      this.rotationAngle = 0; // Re-set the wheel angle to 0 degrees.
      this.wheelSpinning = false; // Reset to false to power buttons and spin can be clicked again.
      this.result = null;
    },

    // -------------------------------------------------------
    // Called when the spin animation has finished by the callback feature of the wheel because I specified callback in the parameters
    // note the indicated segment is passed in as a parmeter as 99% of the time you will want to know this to inform the user of their prize.
    // -------------------------------------------------------
    alertPrize(indicatedSegment) {
      // Do basic alert of the segment text. You would probably want to do something more interesting with this information.
      alert("You have won " + indicatedSegment.text);
    },
  },

  computed: {
    ...mapState(["activeList"]),
  },

  mounted() {
    if (!this.activeList) {
      this.$store.commit("changeActiveList", 0);
    }

    this.createWinwheel();
  },
};
</script>
